import { createSlice } from '@reduxjs/toolkit';

export type mainMenuList = {
  menu_id: string;
  menu_name: string;
  uri: string;
}

export type subMenuList = {
  menu_id: string;
  menu_name: string;
  parent_id: string;
  uri: string;
}

interface DrawerState {
  isOpen: boolean;
  mainMenu: mainMenuList[];
  subMenu: subMenuList[];
}

const fakeMainMenu = [
  {
    menu_id: '1',
    menu_name: 'Dashboard',
    uri: '/home',
  },
  {
    menu_id: '2',
    menu_name: 'Client Profile',
    uri: '/clientProfile',
  },
  {
    menu_id: '3',
    menu_name: 'Maintenance',
    uri: '/maintenance',
  },
];
const fakeSubMenu = [
  {
    menu_id: 'sub1',
    menu_name: 'Create Client',
    parent_id: '2',
    uri: '/clientProfile/createClient',
  },
  {
    menu_id: 'sub2',
    menu_name: 'Search Client',
    parent_id: '2',
    uri: '/clientProfile/searchClient',
  },
  {
    menu_id: 'sub3',
    menu_name: 'Company',
    parent_id: '3',
    uri: '/maintenance/company',
  },
  {
    menu_id: 'sub4',
    menu_name: 'Currency',
    parent_id: '3',
    uri: '/maintenance/currency',
  },
  {
    menu_id: 'sub5',
    menu_name: 'Licensing Right',
    parent_id: '3',
    uri: '/maintenance/licensingRight',
  }
];

const initialState: DrawerState = {
  isOpen: true,
  mainMenu: fakeMainMenu,
  subMenu: fakeSubMenu,
};

const drawerSlice = createSlice({
  name: 'drawer',
  initialState,
  reducers: {
    openDrawer: (state) => {
      state.isOpen = true;
    },
    closeDrawer: (state) => {
      state.isOpen = false;
    },
    toggleDrawer: (state) => {
      state.isOpen = !state.isOpen;
    },
    setMainMenu: (state, action) => {
      state.mainMenu = action.payload;
    },
    setSubMenu: (state, action) => {
      state.subMenu = action.payload;
    },
  },
});

export const { openDrawer, closeDrawer, toggleDrawer, setMainMenu, setSubMenu } = drawerSlice.actions;
export default drawerSlice.reducer;